.breadcrumbs {
    display: flex;
    flex-wrap: wrap;
    font-size: 1.4rem;
    line-height: 1.57;
    list-style: none;
    margin-top: 16px;
    &__item {
        margin-right: 8px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        @include transition();
        color: $white;
        &::after {
            content: "/";
            margin-left: 8px;
            color: $white;
        }
        &:hover {
            text-decoration: none;
            color: $link-color--hover;
            &::after {
                color: $white;
            }
        }
    }
    &__last {
        color: $orange-dim;
    }
}