// Семантические цвета

$base-color:                  #1B1B1B;
$gray:                        #333333;
$gray-middle:                 #878787;
$gray-light:                  #F3F3F3;
$orange:                      #F2BD4B;
$orange-dim:                  #ECBF61;
$white:                       #ffffff;

$cookie-bg: #444444;

$red:                         #F14F41;
$green:                       #11BD6F;

$black-opacity-6:             rgba(0,0,0,0.6);

$white-opacity-15:            rgba(255,255,255,0.15);
$white-opacity-2:             rgba(255,255,255,0.2);
$white-opacity-4:             rgba(255,255,255,0.4);
$white-opacity-6:             rgba(255,255,255,0.6);
$white-opacity-8:             rgba(255,255,255,0.8);

$gray-opacity:                rgba(38, 38, 38, 0.64);
$gray-opacity-light:          rgba(135, 135, 135, 0.4);

$project-overlay-base:        rgba(27, 27, 27, 0.6);
$project-overlay-hover:       rgba(27, 27, 27, 0.9);



$body-bg-color:               #fff;

$link-color:                  $orange;
$link-color--hover:           darken($link-color, 5%);
$link-color--active:          darken($link-color, 10%);


// Базовая типографика

$font-size--root:             10px;

$font-size:                   16px; // rem(16px)
$font-size--small:            80%;

$font-family:                 'Open Sans', Arial, Helvetica CY, Nimbus Sans L, sans-serif;

$font-weight--100:            100;
$font-weight--200:            200;
$font-weight--300:            300;
$font-weight--400:            400;
$font-weight--500:            500;
$font-weight--600:            600;
$font-weight--700:            700;
$font-weight--800:            800;
$font-weight--900:            900;

$font-weight:                 $font-weight--400;

$line-height:                 normal;


// Ширины

$wrapper: (
  'max-width': 1640px,
  'min-width': 280px,
  'padding': 0 20px
);
