.projects {
    position: relative;
    background-color: $base-color;
    padding: 96px 0;
    background-image: url(../images/projects-bg.jpg);
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center center;
    @media (max-width: 1200px) {
        padding: 60px 0;
    }
    @media (max-width: 992px) {
        padding: 40px 0;
    }
    @media (max-width: 768px) {
        padding: 20px 0;
    }
    &--list {
        padding-bottom: 0;
        background-color: transparent;
        background-image: none;
        @media (max-width: 768px) {
            padding-top: 40px;
        }
        &:first-of-type {
            padding-top: 48px;
            @media (max-width: 1200px) {
                padding-top: 40px;
            }
            @media (max-width: 992px) {
                padding-top: 30px;
            }
            @media (max-width: 768px) {
                padding-top: 20px;
            }
        }
        .projects-items {
            margin-top: 4.8rem;
            @media (max-width: 1200px) {
                margin-top: 40px;
            }
            @media (max-width: 768px) {
                margin-top: 30px;
            }
        }
    }

    &-top {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: stretch;
        }
    }

    &-all {
        @include transition();
        display: inline-block;
        position: relative;
        font-size: 1.8rem;
        line-height: 1.44;
        font-weight: 700;
        text-transform: uppercase;
        color: $white;
        white-space: nowrap;
        padding-top: 14px;
        @media (max-width: 768px) {
            align-self: center;
            margin-top: 15px;
            padding-top: 10px;
        }
        &::before {
            @include transition();
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 40px;
            height: 2px;
            background: $orange-dim;
            @media (max-width: 768px) {
                left: calc(50% - 20px);
            }
        }

        &:hover {
            text-decoration: none;
            color: $orange-dim;
            &::before {
                left: calc(100% - 40px);
                background: $white;
            }
        }
    }

    &-items {
        display: flex;
        justify-content: space-between;
        margin-top: 64px;
        @media (max-width: 1200px) {
            margin-top: 30px;
        }
        @media (max-width: 992px) {
            flex-wrap: wrap;
            margin-top: 20px;
        }
    }

    &-item {
        width: calc(100% / 3 - 32px * 2 / 3);
        @media (max-width: 1200px) {
            width: calc(100% / 3 - 20px * 2 / 3);
        }
        @media (max-width: 992px) {
            width: 100%;
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.project {
    display: flex;
    align-items: flex-end;
    position: relative;
    height: 100%;
    overflow: hidden;

    &:hover {
        text-decoration: none;
        .project-overlay {
            background: $project-overlay-hover;
            border-color: $orange-dim;
        }
        .project-title {
            opacity: 0;
        }
        .project-hover {
            opacity: 1;
        }
    }

    &-image {
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: 100%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }

    &-overlay {
        @include transition();
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-width: 0 1px 1px 0;
        border-style: solid;
        border-color: transparent;
        background: $project-overlay-base;
    }

    &-title {
        @include line-top();
        @include transition();
        position: absolute;
        z-index: 3;
        right: 24px;
        bottom: 54px;
        left: 24px;
        font-size: 1.6rem;
        line-height: 1.625;
        font-weight: 700;
        color: $white;
        text-transform: uppercase;
        opacity: 1;
        max-width: 368px;
        @media (max-width: 768px) {
            right: 20px;
            bottom: 30px;
            left: 20px;
        }
    }

    &-hover {
        @include transition();
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        z-index: 4;
        width: 100%;
        height: 100%;
        padding: 84px 24px 26px;
        opacity: 0;
        @media (max-width: 1200px) {
            padding: 60px 20px 20px;
        }
        @media (max-width: 992px) {
            padding: 20px;
        }
        &__title {
            @include line-top();
            position: relative;
            font-size: 1.8rem;
            line-height: 1.44;
            font-weight: 700;
            text-transform: uppercase;
            color: $orange-dim;
            margin-bottom: 40px;
            max-width: 414px;
            @media (max-width: 1200px) {
                margin-bottom: 30px;
            }
            @media (max-width: 992px) {
                margin-bottom: 25px;
            }
            @media (max-width: 768px) {
                max-width: 100%;
                margin-bottom: 15px;
            }
        }

        p {
            font-size: 1.6rem;
            line-height: 1.5;
            color: $white;
            margin-top: 24px;
            max-width: 414px;
            @media (max-width: 1200px) {
                margin-top: 15px;
            }
            @media (max-width: 768px) {
                max-width: 100%;
                margin-top: 10px;
            }
        }

        &__read {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 1.4rem;
            line-height: 1.57;
            letter-spacing: 0.2em;
            text-transform: uppercase;
            margin-top: 102px;
            @media (max-width: 1200px) {
                margin-top: 80px;
            }
            @media (max-width: 992px) {
                margin-top: 30px;
            }
            @media (max-width: 768px) {
                margin-top: 20px;
            }
            &::after {
                @include containBg();
                content: "";
                width: 49px;
                height: 8px;
                background-image: url(../images/project-hover__read-arrow.svg);
            }
        }
    }
}