.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    list-style: none;
    font-size: 1.4rem;
    line-height: 1.57;

    &-item {
        margin: 0 3px;

        &__link {
            @include transition();
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            background: transparent;
            color: $white;

            &:hover, &.active {
                background: $orange-dim;
                color: $white;
                text-decoration: none;
            }
        }

        &__dots {
            color: $white;
        }

        &__button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;

            .icon {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100%;
            }

            svg {
                width: 100%;
            }
        }

        &--back {
            margin-right: 14px;
            @media(max-width: 425px) {
                display: none;
            }

            .icon {
                transform: rotate(180deg);
            }
        }

        &--forward {
            margin-left: 14px;
            @media(max-width: 425px) {
                display: none;
            }
        }
    }
}