.list-page {
    padding-bottom: 96px;
    @media (max-width: 1200px) {
        padding-bottom: 70px;
    }
    @media (max-width: 992px) {
        padding-bottom: 50px;
    }
    @media (max-width: 768px) {
        padding-bottom: 30px;
    }
    .pagination {
        margin-top: 6.4rem;
        @media(max-width: 768px) {
            margin-top: 5rem;
        }
    }
}